бы в Оксфорде Фаулз испытал влияние французского экзистенциализма, самого модного на тот момент философского течения.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"col-md-12"},[_c('h2',{staticClass:"title-bio text-center",attrs:{"id":"thirdChapter"}},[_vm._v("Университетские годы")]),_vm._v(" "),_c('p',{staticClass:"text-bio"},[_c('span',{staticClass:"redLine"},[_vm._v("После получения в 1950 ")]),_vm._v("степени бакалавра гуманитарных наук, Фаулз работает преподавателем в университете г.Пуатье во Франции, а позже в гимназии для мальчиков в колледже Анаргириос на греческом острове Спетцай. Там же он встретил свою будущую жену Элизабет Уиттон, в 1956 году они поженились. В Англии Фаулз преподает в колледже Эшбридж (1953-54) и в колледже Сент-Годрик (1954-63). В это же время он работает над несколькими романами, в том числе над “Волхвом”, который он переписывал в течение 13 лет.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"col-md-12"},[_c('h2',{staticClass:"title-bio text-center",attrs:{"id":"fourthChapter"}},[_vm._v("Творческий дебют")]),_vm._v(" "),_c('p',{staticClass:"text-bio"},[_c('span',{staticClass:"redLine"},[_vm._v("Дебютным романом Фаулза")]),_vm._v("  стал триллер “Коллекционер”, вышедший в свет в 1963 году. Джуд Кинберг и Джон Кон, сценаристы с телевидения, выкупили права на сценарий книги до ее публикации. Вильям Вайлер согласился снимать фильм. Он признавался: “Когда читал, я удивился, что не могу оторваться”\n      ")])]),_vm._v(" "),_c('div',{staticClass:"col-md-12"},[_c('h2',{staticClass:"title-bio text-center",attrs:{"id":"fifthChapter"}},[_vm._v("Коллекционер")]),_vm._v(" "),_c('p',{staticClass:"text-bio"},[_c('span',{staticClass:"redLine"},[_vm._v("\"Коллекционер\"")]),_vm._v(" принес Фаулзу большой успех, с тех пор он посвящает себя сочинительству целиком. Фреди Клег, от чьего лица ведется повествование - молодой человек, потерявший родителей, коллекционер бабочек. Выиграв в национальной футбольной лотерее, он покупает заброшенный особняк в стиле тюдор, с подвалом как в настоящей крепости. Он похищает и заключает в него молодую женщину, Миранду Грэй, симпатичную студентку-художницу. Миранда ведет дневник, куда заносит все разговоры с преступником и планы побега, в то время как Клег надеется завоевать ее \"уважение\". Ей удается одержать не одну маленькую победу, но не свободу, в конце концов, она умирает от воспаления легких. У этого сюжета два источника возникновения. Во-первых, Фаулз видел оперу Бартока \"Замок герцога Синяя Борода\", в которой мужчина заточал в подвале замка женщин, а во-вторых, он читал реальную историю о том, как лондонский мальчишка несколько месяцев держал в бомбоубежище девочку.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"col-md-12"},[_c('h2',{staticClass:"title-bio text-center",attrs:{"id":"sixthChapter"}},[_vm._v("Волхв")]),_vm._v(" "),_c('p',{staticClass:"text-bio"},[_c('span',{staticClass:"redLine"},[_vm._v("Во втором романе")]),_vm._v("  (\"Волхв\") Фаулз использовал элементы пьесы Шекспира \"Буря\" (1623). Черновым названием романа было название \"Godgame\" - букв. \"Игра Бога\", в нем заметно влияние Карла Юнга и таких романов Генри Джеймса и Диккенса как \"Поворот винта\" и \"Большие надежды\".\n      ")])]),_vm._v(" "),_c('div',{staticClass:"col-md-12"},[_c('h2',{staticClass:"title-bio text-center",attrs:{"id":"seventhChapter"}},[_vm._v("Женщина французского лейтенанта")]),_vm._v(" "),_c('p',{staticClass:"text-bio"},[_c('span',{staticClass:"redLine"},[_vm._v("\"Женщина французского лейтенанта\".")]),_vm._v("  Место действия романа - Лайм Регис в 60-е годы 19 века, в нем воссоздана романтика викторианской эпохи, мира Томаса Гарди. Палеонтолог-любитель Чарлз Смитсон, богатый человек и последователь эволюционной теории Дарвина, влюбляется в Сару Вудруф, темпераментную гувернантку, которую якобы бросил французский лейтенант.\n      ")])]),_vm._v(" "),_c('div',{staticClass:"col-md-12"},[_c('h2',{staticClass:"title-bio text-center",attrs:{"id":"eighthChapter"}},[_vm._v